import React from 'react';
import {Link} from 'react-router-dom';
import '../components_css/Video.css';
import bgvideo from '../images/bg-video.mp4'



class Video extends React.Component {
  constructor(props){
    super(props);
    this.show_on_full = props.show_on_full;
  }

  render() {
    const show_class = this.show_on_full;
    return(
      <video loop autoPlay muted id='bg-video'> <source src={bgvideo} type="video/mp4"/> </video>
    )};
}

export default Video;
